// logo file
.logo {
    width: 4cm;
    height: auto;
    cursor: pointer;
}

// navigation bar container
.Nav {
    margin: .5cm 0cm;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

// navigation menu
.menu {
    display: flex;
    align-items: center;
    column-gap: .5cm;
}

// design for selected menu item
.menu-item-nav {
    text-decoration: underline;
    text-decoration-color: rgb(0, 0, 0);
    text-decoration-thickness: 2px;
    text-underline-position: under;
}

.overlay-nav-mobile {

    position: fixed;

    bottom: 0;
    z-index: 100;
    top: 0;
    right: 0;
    backdrop-filter: blur(10px);
    display: flex;
    justify-content: flex-end;
    transition: .2s;
    width: 0%;
}

.close-button {
    padding: .25cm .5cm;
    font-size: 15px;
    border: thin solid rgb(64, 64, 64);
    border-radius: 500px;
    cursor: pointer;
    background-color: #0f0f13;
    color: white;
    text-align: center;

    &:hover {
        box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
        background-color: rgb(194, 12, 36);
        border: thin solid #0f0f13;
    }
}

.overlay-menu {
    padding-top: 1cm;
    width: 5cm;
    height: 100%;
    overflow-y: auto;
    border: 1px solid rgb(64, 64, 64);
    background-color: #0f0f13;
    display: flex;
    flex-direction: column;
    row-gap: .3cm;
    align-items: center;
    justify-content: flex-start;
}

.menu-itemO {
    font-size: 20px;
    font-family: kalam;
    cursor: pointer;
    color: white;
}

.logo-mobile {
    width: 2cm;
}

.menu-item-navO {
    text-decoration: underline;
    text-decoration-color: green;
    text-decoration-thickness: 4px;
    text-underline-position: under;
    text-decoration-style: dashed;
}

.menuG {
    width: .6cm;
    height: .6cm;
    display: none;
}

@media only screen and (max-width:900px) {


    .logo {
        width: 3.5cm;
    }

}

@media only screen and (min-width : 600px) {
    .menuG {
        display: none;
    }

    #overlay-nav {
        width: 0%;
        display: none;
    }
}

@media only screen and (max-width:600px) {
    .menu-item {
        display: none;
    }

    .logo {
        width: 3cm;
    }

    .menuG {
        display: block;
    }
}

@media only screen and (max-width:500px) {


    .logo {
        width: 2.7cm;
    }

}

@media only screen and (max-width:500px) {
    .overlay-menu {
        width: 100%;
    }
}