* {
  text-decoration: none;
  color: black;
  margin: 0px 0px;
  padding: 0px 0px;
}

.App {
  padding: 0vh 0vw;
}

.Home,
.Nav,
.Courses,
.Testimonials,
.About {
  padding: 0vh 10vw;
}

body {
  min-height: 100vh;
}


@media only screen and (min-width : 1700px) {

  .Home,
  .Nav,
  .Courses,
  .Testimonials,
  .About {
    padding: 0vh 18vw;
  }
}

