.ResearchAnalyst {
    padding: 0vh 10vw;
    margin: 2cm 0cm 2cm 0cm;
}

.research-analyst-box {
    // border: 1px solid black;
    display: flex;
    align-items: flex-start;
    column-gap: 2cm;
}

.course-left {
    width: 20cm;
    // border: 1px solid black;
}

.course-right {
    width: 10cm;
    // border: 1px solid black;
}

.course-page-name {
    font-size: 42px;
    font-weight: bolder;
    font-family: Merriweather;
}

.course-overview-video {
    width: 100%;
    height: 7cm;
}

.course-preview-video {
    width: 100%;
    height: 13cm;
}

.course-dets-price-section {
    display: flex;
    align-items: center;
    column-gap: .2cm;
}

.course-desc {
    text-decoration: line-through;
}

.course-dets-right {
    margin-top: .3cm;
}

.what-learn {
    width: 100%;
    // border: 1px solid black;
}

.get-list-box-course {
    display: flex;
    flex-wrap: wrap;
    column-gap: .7cm;
    margin-top: .05cm;
    row-gap: .05cm;
}

.head-learn {
    font-size: 24px;
    font-family: sarabun;
    font-weight: 600;
}

.videos-head {
    font-size: 25px;
    font-weight: 700;
    font-family: sarabun;
}

.video-box {
    margin-top: .2cm;
    display: flex;
    flex-direction: column;
    row-gap: .3cm;
}

.chapter-box {
    padding: .45cm 0.4cm;
    border: 1px solid rgb(201, 201, 201);
    background-color: whitesmoke;

    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}

.chapter-name {
    font-size: 20px;

    font-weight: 600;
}

.video-name {
    font-size: 18px;

    cursor: pointer;


    &:hover {
        text-decoration: underline;
    }
}

.video-box-course {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: .2cm 0cm;
    border-bottom: 1px solid rgb(151, 151, 151);
}

.videos {
    margin-top: .1cm;
    display: flex;
    flex-direction: column;
    row-gap: .05cm;
}

.chapter {
    display: flex;
    justify-content: space-between;
    cursor: pointer;

}

.cpdf {
    cursor: pointer;
    width: .7cm;
}

#overlayVid {
    display: none;
}

.overlay-vid {

    position: fixed;

    bottom: 0;
    z-index: 100;
    top: 0;
    right: 0;
    backdrop-filter: blur(10px);
    display: flex;
    justify-content: center;
    align-items: center;
    transition: .2s;
    width: 100%;
}

.video-preview {
    width: 20cm;
    background-color: rgb(176, 176, 176);
    padding: 0.5cm .7cm;
    height: fit-content;
    border: 1px solid rgb(134, 134, 134);
    backdrop-filter: blur(10px);
}

.video-head-section {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: .3cm;
}

.clse {
    width: .5cm;
    cursor: pointer;
}

#oneVideo,
#TwoVideo,
#ThreeVideo {
    display: none;
}